<template>
  <div>
      <H1>프로필 사진을 업로드해주세요.</H1>
      <div class="form-wrap">
          <div class="imgfile">
            <input v-on:change="fileSelect()" type="file" ref="userImage" />
            
          </div>
        </div>
        <br>
         
        <button id="show-modal" class="btn" @click="updateInfo()">
            업로드하기
          </button>
  </div>
</template>

<script>
import axios from "axios";
// import firebase from 'firebase';
export default {
    methods:{
 fileSelect: function() {
      
      this.userImage = this.$refs.userImage["files"][0];
     
    },
     updateInfo() {
      let vm = this;
      
      const formData = new FormData();
      // var user = firebase.auth().currentUser; 
      // var uid = user.uid;
      // formData.append("uid",user.uid);
      formData.append("userImage", vm.userImage);
      formData.append("userIntroduce",vm.userIntroduce);
      formData.append("userName",vm.userName);
      formData.append("userId",vm.userId);
      formData.append("userPassword",vm.userPassword);
      formData.append("userEmail",vm.userEmail);
      console.log(formData);
    
     for (var pair of formData.entries()) {
        console.log(pair[0] + ", " + pair[1]);
      }
    
      axios
        .post(`http://localhost:/user/profile/update`,formData,{
          headers: {
            "Content-Type": "multipart/form-data",
          },
        }
        )
        .then((response) => {
         console.log(response.data);
         alert("프로필 업로드 완료")
        });
    },
    },
    data(){
        return{
           userImage:"",
           userIntroduce: "test",
           userName:"taek12",
           userId:"3",
           userPassword:"ssafy",
           userEmail:"wecandoit@naver.com"
        }
    }
}
</script>

