<template>
  <div>
      <h1>로그인</h1>
         <input
          v-model="userEmail"
          id="userEmail"
          placeholder="이메일을 입력하세요."
          type="text"
        />
        <br>
       
        <br>
         <input
          v-model="userPassword"
          id="userPassword"
          placeholder="비밀번호를 입력하세요."
          type="password"
        />
        <br>
        <button @click="onLogin">로그인</button>
        <button @click="onJoin">회원가입</button>


  </div>
  
  
</template>

<script>
 import axios from 'axios'
export default {
   data: () => {
       return {
       userEmail:"",
       userPassword:"",
       
       user:{
           userEmail:"",
           userPassword:""
       }
       }
   },
    methods:{
        onLogin(){
           var vm = this;
           axios
            .get(`http://localhost:/user/login`,
            {
                params:{
                    userEmail:vm.userEmail,
                    userPassword:vm.userPassword
                }
            }
                
            )
            .then((response) => {
                
                this.$router.push("/feed");
                
                
            })
            .catch((error) => {
                console.log("에러입니다")
            }) 
        },
        onJoin(){
            this.$router.push("/join");
        }
    }

}
</script>

