<template>
  <div>
      <input
          v-model="userEmail"
          id="userEmail"
          placeholder="이메일을 입력하세요."
          type="text"
        />
        <br>
        <input
          v-model="userName"
          id="userName"
          placeholder="이름을 입력하세요."
          type="text"
        />
        <br>
         <input
          v-model="userPassword"
          id="userPassword"
          placeholder="비밀번호를 입력하세요."
          type="password"
        />
        <br>
        <button @click="onJoin">가입하기</button>
  </div>
</template>

<script>
import axios from 'axios'
export default {
data: () => {
       return {
           user:{
       userEmail:"",
       userPassword:"",
       userName:"",
           },
    userEmail:"",
       userPassword:"",
       userName:"",
       }
   },
   methods:{
       onJoin(){
           var vm = this;
        //    vm.user.userEmail = vm.userEmail;
        //    vm.user.userName = vm.userName;
        //    vm.user.userPassword = vm.userPassword;
             axios.post('http://localhost/user/register',{
                
             
          userName : vm.userName,
          userEmail : vm.userEmail,
          userPassword : vm.userPassword  } 
                )
      .then(function (response) {
        console.log(response);
      })
      .catch(function (error) {
        console.log(error);
      });

       }
   }
}
</script>

