<template>
  <div>
      <h1>프로필</h1>
        <img :src="userImage" alt="프로필 이미지"><br>
        <!-- <img :src = "require(${photourl})"/> -->
        <p>{{userName}}</p>
        <p>{{userIntroduce}}</p>      
  </div>
</template>

<script>
import axios from 'axios'
export default {
    created () {
           axios
            .get(`http://localhost:/user/profile?userId=${this.userId}`)
            .then((response) => {
                this.userImage = response.data.userImage
                this.userName = response.data.userName
                this.userIntroduce = response.data.userIntroduce
                
            })
            .catch((error) => {
                console.log(error)
            })
    },

 data(){
        return{
           userImage:"",
           userIntroduce: "",
           userName:"",
           userId:"3",
           userPassword:"",
           userEmail:"",
        //    photoUrl:"/var/www/html/dist/img/" + userImage
        }
    },
   
}
</script>

